      var id = this.doctorId.id;
      var parme = {}
      if (this.doctorId.userType == 1 && this.doctorId.roleId !=="1312") {
        parme.doctorId = this.doctorId.id;
      }
      if (this.doctorId.userType == 2 && this.doctorId.roleId !=="1313") {
        parme.expertId = this.doctorId.id;
        parme.state = "1,2,3,4,6";
        this.Parma.state="1,2,3,4,6";
        this.expertId=this.doctorId.id
      }
      if(this.doctorId.userType == 3 && this.Parma.hospitalId ){
        parme.hospitalId=this.Parma.hospitalId
      }
      if (this.doctorId.roleId =="1312") {

      }
      if (this.doctorId.roleId =="1313") {
        parme.state = "1,2,3,4,6";
        this.expertId="";
        this.Parma.state="1,2,3,4,6";
      }
      if (this.Parma.startDate) {
        if (!this.Parma.length) {
          parme.startDate = moment(this.Parma.startDate).format('YYYY-MM-DD')
        }
      }
      if (this.Parma.endDate) {
        if (!this.Parma.length) {
          parme.endDate = moment(this.Parma.endDate).format('YYYY-MM-DD')
        }
      }
      if (this.Parma.type) {
        parme.type = this.Parma.type;
      }
      if (this.Parma.type == 0) {
        parme.type = this.Parma.type;
      }
      if (this.Parma.search) {
        parme.search = this.Parma.search;
      }
      if (this.yn == 2 && a == 4) {
        return
      }
      if (a == 4) {
        this.yn = 2
      }
      var url = '/api/assist-order/assistOrders/statuscount';
      var token = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        },
        params: parme
      }).then(function(res) {
        res = res.body;
        for (let i in this.tabList) {
          this.tabList[i].number = 0
        }
        var cunt = 0;
        for (let i in res) {
          cunt += parseInt(res[i].statusCount);
          switch (res[i].status) {
            case 0:
              res[i].text = "待付款";
              break;
            case 1:
              res[i].text = "已预约";
              break;
            case 2:
              res[i].text = "协诊中";
              break;
            case 3:
              res[i].text = "已完成";
              break;
            case 4:
              res[i].text = "未出诊";
              break;
            case 5:
              res[i].text = "失效";
              break;
            case 6:
              res[i].text = "已确认";
              break;
          }
          for (let k in this.tabList) {
            if (res[i].status == this.tabList[k].status) {
              this.tabList[k].number = parseInt(res[i].statusCount)
            }
          };
        }
        if (!this.tabList[0].first) {
          if (!this.Parma.state && this.Parma.state != 0) {
            this.tabList.unshift({ text: "全部", check: true, itemKey: null, number: cunt, status: null, first: 1 });
          } else {
            this.tabList.unshift({ text: "全部", check: true, itemKey: null, number: cunt, status: null, first: 1 });
          }
        } else {
          this.tabList[0].number = cunt
        }
        if (this.tabList[1].itemKey == 0) {
          if (this.doctorId.userType == 2 || this.doctorId.isAdmin) {
            this.tabList.splice(1, 1)
          }
        }
        this.listOder(a);
      });
